import React from "react"
import { Link } from "gatsby"
import { GatsbyImage, getImage, getSrc, StaticImage } from "gatsby-plugin-image"
import Layout from "../components/layout"
import Seo from "../components/seo"
import "../styles/main-article-column-template.css"

export default function Report({ pageContext }) {
  const { slug, outline, title, updatedAt } = pageContext.report
  const image1 = getImage(pageContext.report.image1)
  const imageUrl = getSrc(pageContext.report.image1)
  const body1 = pageContext.report.body1.childMarkdownRemark.html
  const image2 = getImage(pageContext.report.image2)
  const twitterUrl = `https://twitter.com/share?url=https://www.hotsoul.jp/dx-council/${slug}`
  const facebookUr = `http://www.facebook.com/share.php?u=https://www.hotsoul.jp/dx-council/${slug}`
  const lineUrl = `https://social-plugins.line.me/lineit/share?url=https://www.hotsoul.jp/dx-council/${slug}`
  const description = `やさしいDX推進協議会の活動報告です。${outline}`
  const pageUrl = `https://www.hotsoul.jp/dx-council/${slug}`

  return (
    <Layout>
      <Seo description={description} title={title} imageUrl={imageUrl} pageUrl={pageUrl} />
      <div className="wrap">
        <section className="mv_ac-cm">
          <div className="mv_ac-cm-inner">
            <p className="txt1"><span>活動報告</span></p>
            <p className="txt2">やさしいDX推進協議会の<br/>活動報告をご紹介します。</p>
          </div>
        </section>

        <section className="ac-cm">
          <h2>{title}</h2>
          <div className="ac-cm-cont">
            <div className="ac-cm-inner">
              <div className="item-box">
                <p className="postdate">{updatedAt}</p>
                <div className="item no-border first">
                  <div className="large-img">
                    <GatsbyImage image={image1} alt="イメージ1"/>
                  </div>
                  <div dangerouslySetInnerHTML={{ __html: body1 }} />
                </div>

                <div className="item no-border">
                  <div className="large-img">
                    <GatsbyImage image={image2} alt="イメージ2"/>
                  </div>
                </div>

                <div className="flex-share">
                  <ul className="sns">
                    <li>
                      <a href={twitterUrl} rel="nofollow noopener noreferrer" target="_blank">
                        <StaticImage
                          src="../images/icon-tw.png"
                          alt="twitterシェア"
                          placeholder="blurred"
                        />
                      </a>
                    </li> 
                    <li>
                      <a href={facebookUr} rel="nofollow noopener noreferrer" target="_blank">
                        <StaticImage
                          src="../images/icon-fb.png"
                          alt="facebookシェア"
                          placeholder="blurred"
                        />
                      </a>
                    </li>
                    <li>
                      <a href={lineUrl} rel="nofollow noopener noreferrer" target="_blank">
                        <StaticImage
                          src="../images/icon-line.png"
                          alt="LIMEシェア"
                          placeholder="blurred"
                        />
                      </a>
                    </li>
                  </ul>
			          </div>
                <br/>                
                <p className="archive"><Link to={`/dx-council/articles`}>他の記事も読む</Link></p>

              </div>
            </div>
          </div>
        </section>
      </div>
    </Layout>
  )
}